.status-alerts-wrapper {
  z-index: 99999;
  width: 100%;
  height: 0;
  position: fixed;
  top: 0;
  left: 0;
}

.status-alerts-wrapper * {
  box-sizing: border-box;
}

.status-alert {
  z-index: 1;
  width: 100%;
  max-width: 780px;
  max-height: 600px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 14px;
  padding-right: 14px;
  transition: transform .5s ease-out, opacity .8s, max-height .8s ease-out, -webkit-transform .5s ease-out;
  display: block;
  position: relative;
}

.status-alert.is-transparent {
  opacity: 0;
  max-height: 0;
  transform: translate(0, -100%);
}

.status-alert.is-transparent.is-hidden {
  display: none;
}

