  border-radius: 5px;
  min-height: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 14px 60px 17px 46px;
  display: block;
  position: relative;
  box-shadow: 0 15px 65px -9px #2e333ad9;
}

.status-alert__box.is-green-success {
